<template>
  <div class="contentHtml">
	<!--<van-swipe :autoplay="3000">
	  <van-swipe-item v-for="img in images">
	  	<img class="width100" :src="img" />
	  </van-swipe-item>
	</van-swipe>-->
	<ul class="flex iconType">
		<li>
			<img v-on:click="hrefTo('hrefTo','/index/transfer/transferredDetail','')" src="../../../assets/chushouwangdian@3x.png"/>
			<p></p>
		</li>
		<!--<li>
			<img v-on:click="hrefTo('hrefTo','/index/transfer/transferredDetail')" src="../../../assets/jingbiaohuida@3x.png"/>
			<p></p>
		</li>-->
		<li>
			<img v-on:click="hrefTo('hrefTo','/index/transfer/evaluation','')" src="../../../assets/wangdianpinggu@3x.png"/>
			<p></p>
		</li>
		<!--<li>
			<img v-on:click="hrefTo('hrefTo','href')" src="../../../assets/jingbiaohuida@3x.png"/>
			<p></p>
		</li>-->
	</ul>
	<div class="initBox text-c oh line font16">
		<div class="width50 fl" v-on:click="popShow('wangdian')">{{wangdian.val}}</div>
		<div class="width50 fl" v-on:click="popShow('provinceList')">{{provinceList.val}}</div>
	</div>
  	
  	<ul class="listBox">
  	  	<template v-for="listData in listDatas">
	  		<li v-on:click="hrefTo('hrefTo','/index/transfer/shopInfo/'+listData.id,'')">
	    			<div class="leftBox">
	    				<div class="listTitle">{{listData.title}}</div>
	    				<div class="colorA">{{listData.price2}}</div>
	    				<div class="listInfo">
	    					<template v-for="tag in listData.tags">
	    						<span class="but1">{{tag}}</span>&emsp;
	    					</template>
	    					<span>{{listData.shop_type  | typeFormatter}}&emsp; {{listData.intime | timeFormatter}}&emsp;</span>
	    				</div>
	    			</div>
	    			<div class="rightBox"><img v-bind:src="listData.shop_img_url"></div>
	    		</li>
    		</template>
     </ul>
     <div class="lodingDiv loding"></div>
     <!---->
	 <van-popup v-model="wangdian.type" position="bottom" :overlay="true">
		<van-picker :columns="wangdian.list" show-toolbar :title="wangdian.title" @cancel="onCancel()" @confirm="wangdianOnConfirm" /> 
	</van-popup>
	<!---->
	 <van-popup v-model="provinceList.type" position="bottom" :overlay="true">
		<van-picker :columns="provinceList.list" show-toolbar :title="provinceList.title" @cancel="onCancel()" @confirm="provinceListOnConfirm" /> 
	</van-popup>
  </div>
</template>

<script>
$("title").text("");
export default {
  name: 'contentHtml',
  data () {
    return {
    	images: [
        'https://img.yzcdn.cn/2.jpg',
        'https://img.yzcdn.cn/2.jpg',
        'https://img.yzcdn.cn/2.jpg',
        'https://img.yzcdn.cn/2.jpg'
    ],
    	listDatas:[],
    	input:'',
    //
	wangdian: {
		"list":['','', '','', '','', '6','','800'],
		"title":"",
		"val":"",
		"indexList":[-1,1,2,3,4,5,6,7,8],
		"index":-1,
		"type":false
	},
	//
	provinceList: {
		"list":[
			'',
			'','','','','','','','','','',
			'','','','','','','','','','',
			'','','','','','','','','','',
        		'','','',''
		],
		"title":"",
		"val":"",
		"indexList":[
			'-1',
			'110000','120000','130000','140000','150000','210000','220000','230000','310000','320000',
    			'330000','340000','350000','360000','370000','410000','420000','430000','440000','450000',
    			'460000','500000','510000','520000','530000','540000','610000','620000','630000','640000',
    			'650000','710000','810000','820000'
		],
		"index":'-1',
		"type":false
	},
    	message:'',
    	this_page:1
    }
  },
  created:function(){
  	var _this = this;
  	//
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //
	  var windowH = $(window).height(); //
	  var height = $(document).height();  //
	  if(top+windowH==height){
	  	var addHtml = $(".lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		_this.getList();
	  		return false;
	  	}
	  }
	})
	
	_this.getList();
  },
  methods: {
  	getList:function(){
  		var _this = this;
  		$(".lodingDiv").text('').addClass("loding").show();
    		var start = _this.listDatas.length;
    		var per_page = 20;
    		var data = {
	        'pageIndex':start,
	        'pageSize':per_page,
	        'username':"",   //
	        'nickname':"",   //
	        'public_time_start':"",  //
	        'pulbic_time_end ':"",  //
	        'shop_type':_this.wangdian.index,  //
	        'shop_addr':_this.provinceList.index,  //
	        'title':"",   //
	        'sh_status':1  //0
	    };
	  	$.get(_this.apiUrl+'/e/wxapi/shoplist.php',data,function(res){
	  		$(".lodingDiv").hide();
	  		console.log(res)
	  		var start = _this.listDatas.length;
			for(var i = 0; i < res.length; i++) {
		      _this.listDatas[start+i] = res[i];
		    }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".lodingDiv").text("").removeClass("loding").show();
				var lengths = $(".listBox li").length;
			}
		},'json')
  	},
	hrefTo(type,val,title){
		//ios
		if(this.utilIsLocalhost()==false){
			if(type=="hrefTo"){
				var content = {"type":"hrefTo","link":val,"title":title,}
			}else{
				var content = {"type":"href","id":val}
			}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	  	}else{
	  		if(type=="hrefTo"){
	  			this.$router.push(val)
	  		}else{
	  			this.$router.push('/index/transfer/shopInfo/'+val)
	  		}
  		}
	},
    popShow: function (type) {
    		switch(type){
		    case "wangdian":
		        this.wangdian.type = true;
		        break;
		    case "provinceList":
		        this.provinceList.type = true;
		        break;
		    default:
		        ""
		}
  		this.bankCard = false;
  	},
  	wangdianOnConfirm(value, index) {
      this.wangdian.val = value;
      this.wangdian.index = this.wangdian.indexList[index];
      this.wangdian.type = false;
      this.listDatas = [];
      this.getList();
    },
  	provinceListOnConfirm(value, index) {
      this.provinceList.val = value;
      this.provinceList.index = this.provinceList.indexList[index];
      this.provinceList.type = false;
      this.listDatas = [];
      this.getList();
   },
    onCancel(type) {
    		this.wangdian.type = false;
    }
  }
}

import Vue from 'vue'
Vue.filter('timeFormatter',function (value){
	return value.substring(5,10);
});
Vue.filter('typeFormatter',function (value){
	if(value==1){
		return "1";
	}else if(value==2){
		return "2";
	}else if(value==3){
		return "3";
	}
});

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.iconType{text-align: center;font-size: 1.3rem;background: #fff;margin-bottom:10px;}
	.iconType li{width:50%;padding:15px;}
	.iconType li img{width:3rem;height: 3rem;}
</style>



