import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import information from '@/pages/information'
import informationDetail from '@/pages/informationDetail'
import addPost from '@/pages/addPost'

import shopInfo from '@/pages/index/transfer/shopInfo'   //
import transferred from '@/pages/index/transfer/transferred'   //
import transferredDetail from '@/pages/index/transfer/transferredDetail'  //
import transferredOrder from '@/pages/index/transfer/order'  //
import evaluation from '@/pages/index/transfer/evaluation'  //

import bidding from '@/pages/index/bidding/bidding'  //
import biddingAdd from '@/pages/index/bidding/biddingAdd'  //
import biddingDetail from '@/pages/index/bidding/biddingDetail'  //
import canyujb from '@/pages/index/bidding/canyujb'  //


import answer from '@/pages/index/answer/answer'  //
import answerAdd from '@/pages/index/answer/answerAdd'  //
import answerDetail from '@/pages/index/answer/answerDetail'  //

import classroom from '@/pages/index/classroom/classroom'  //
import classroomType from '@/pages/index/classroom/classroomType'  //
import classroomAdd from '@/pages/index/classroom/classroomAdd'  //
import classroomDetail from '@/pages/index/classroom/classroomDetail'  //

import recruit from '@/pages/index/recruit/recruit'  //
import recruitAdd from '@/pages/index/recruit/recruitAdd'  //
import recruitDetail from '@/pages/index/recruit/recruitDetail'  //


import circle from '@/pages/circle/circle'  //
import myCircle from '@/pages/circle/myCircle'  //
import circleAdd from '@/pages/circle/circleAdd'  //
import circleDetail from '@/pages/circle/circleDetail'  //
import circleFind from '@/pages/circle/circleFind'  //
import circleSet from '@/pages/circle/circleSet'  //
import circleNews from '@/pages/circle/circleNews'  //
import circleNewsAdd from '@/pages/circle/circleNewsAdd'  //


import User from '@/pages/user/user' //
import collection from '@/pages/user/collection' //
import follow from '@/pages/user/follow' //
import certification from '@/pages/user/certification' //
import release from '@/pages/user/release' //

import order from '@/pages/user/order' //
import mjOrder from '@/pages/user/mjOrder' //
import offerOrder from '@/pages/user/offerOrder' //
import orderDetail from '@/pages/user/orderDetail' //


Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/information',
      name: 'information',
      component: information
    },
    {
      path: '/post/add',
      name: 'addPost',
      component: addPost
    },
    {
      path: '/information/:id',
      name: 'informationDetail',
      component: informationDetail
    },
    {
      path: '/user/user',
      name: 'User',
      component: User
    },
    {
      path: '/user/collection',
      name: 'collection',
      component: collection
    },
    {
      path: '/user/follow',
      name: 'follow',
      component: follow
    },
    {
      path: '/user/certification',
      name: 'certification',
      component: certification
    },
    {
      path: '/user/release',
      name: 'release',
      component: release
    },
    {
      path: '/user/order',
      name: 'order',
      component: order
    },
    {
      path: '/user/mjOrder',
      name: 'mjOrder',
      component: mjOrder
    },
    {
      path: '/user/offerOrder',
      name: 'offerOrder',
      component: offerOrder
    },
    {
      path: '/user/order/orderDetail/:id',
      name: 'orderDetail',
      component: orderDetail
    },
    
    {
      path: '/index/transfer/shopInfo/:id',
      name: 'shopInfo',
      component: shopInfo
    },
    {
      path: '/index/transfer/transferred',
      name: 'transferred',
      component: transferred
    },
    {
      path: '/index/transfer/transferredDetail',
      name: 'transferredDetail',
      component: transferredDetail
    },
    {
      path: '/index/transfer/order',
      name: 'transferredOrder',
      component: transferredOrder
    },
    {
      path: '/index/transfer/evaluation',
      name: 'evaluation',
      component: evaluation
    },
    {
      path: '/index/bidding/bidding',
      name: 'bidding',
      component: bidding
    },
    {
      path: '/index/bidding/biddingAdd',
      name: 'biddingAdd',
      component: biddingAdd
    },
    {
      path: '/index/bidding/biddingDetail/:id',
      name: 'biddingDetail',
      component: biddingDetail
    },
    {
      path: '/index/bidding/canyujb/:id',
      name: 'canyujb',
      component: canyujb
    },
    {
      path: '/index/answer/answer',
      name: 'answer',
      component: answer
    },
    {
      path: '/index/answer/answerAdd',
      name: 'answerAdd',
      component: answerAdd
    },
    {
      path: '/index/answer/answerDetail/:id',
      name: 'answerDetail',
      component: answerDetail
    },
    {
      path: '/index/classroom/classroom',
      name: 'classroom',
      component: classroom
    },
    {
      path: '/index/classroom/classroomAdd',
      name: 'classroomAdd',
      component: classroomAdd
    },
    {
      path: '/index/classroom/classroomType/:id',
      name: 'classroomType',
      component: classroomType
    },
    {
      path: '/index/classroom/classroomDetail/:id',
      name: 'classroomDetail',
      component: classroomDetail
    },
    {
      path: '/index/recruit/recruit',
      name: 'recruit',
      component: recruit
    },
    {
      path: '/index/recruit/recruitAdd',
      name: 'recruitAdd',
      component: recruitAdd
    },
    {
      path: '/index/recruit/recruitDetail/:id',
      name: 'recruitDetail',
      component: recruitDetail
    },
    {
      path: '/circle/circle',
      name: 'circle',
      component: circle
    },
    {
      path: '/circle/myCircle',
      name: 'myCircle',
      component: myCircle
    },    
    {
      path: '/circle/circleAdd',
      name: 'circleAdd',
      component: circleAdd
    },
    {
      path: '/circle/circleDetail/:id',
      name: 'circleDetail',
      component: circleDetail
    },
    {
      path: '/circle/circleFind',
      name: 'circleFind',
      component: circleFind
    },
    {
      path: '/circle/circleSet',
      name: 'circleSet',
      component: circleSet
    },
    {
      path: '/circle/circleNews',
      name: 'circleNews',
      component: circleNews
    },
    {
      path: '/circle/circleNewsAdd/:id',
      name: 'circleNewsAdd',
      component: circleNewsAdd
    }
  ]
})



