
<template>
  <div class="contentHtml">
  	  <van-cell-group>
	  	<van-cell title=""  v-bind:value="orderDetail.order_num" />
	  	<van-cell title=""  v-bind:value="orderDetail.public_time" />
	  	<van-cell title=""  v-bind:value="orderDetail.title" />
	  	<van-cell>
		    <template slot="title">
		      <span class="van-cell-text"></span>
		    </template>
		    <span>{{orderDetail.price}}</span>
  		</van-cell>
  		<van-cell>
		    <template slot="title">
		      <span class="van-cell-text"></span>
		    </template>
		    <span>{{orderDetail.bond}}</span>
  		</van-cell>
  		<van-cell>
		    <template slot="title">
		      <span class="van-cell-text"></span>
		    </template>
		    <span>{{orderDetail.technical_annual_fee}}</span>
  		</van-cell>
  		<van-cell>
		    <template slot="title">
		      <span class="van-cell-text"></span>
		    </template>
		    <span>{{orderDetail.technical_annual_fee}}</span>
  		</van-cell>
  		<van-cell>
		    <template slot="title">
		      <span class="van-cell-text"></span>
		    </template>
		    <span>{{(orderDetail.price*1)+(orderDetail.technical_annual_fee)*1}}</span>
  		</van-cell>
	  </van-cell-group>
	  <div style="height: 10px;"></div>
	  <van-cell-group>
	  	<van-cell>
		    <template slot="title">
		      <span class="van-cell-text"></span>
		    </template>
		    <span>{{orderDetail.money}}</span>
		    <span></span>
  		</van-cell>
	  	<van-cell>
	    	  <template slot="title">
	        <span class="van-cell-text"></span>
	      </template>
	      <van-radio name="1" v-model="radio"></van-radio>&nbsp;&nbsp;
	      <van-radio name="2" v-model="radio"></van-radio>
	    </van-cell>
	  </van-cell-group>
	  <div style="line-height:40px;color: #999;"></div>
	  <van-cell-group>
	  	<van-field  label="" placeholder="" required/>
	  </van-cell-group>
	  <div style="height: 10px;"></div>
	 <div style="background: #fff;padding:20px 0;">
	 	<div class="but-80-red" v-on:click="onSubmit"></div>
	 </div>
  </div>
</template>

<script>
setTimeout(function(){
	$("title").text("");
},1)
export default {
  name: 'contentHtml',
  data () {
    return {
    		orderDetail:'',
    		radio:'1'
    }
  },
  created:function(){
  	var _this = this;
  	var obj = {"oid":_this.$route.params.id};
    $.ajax({
        type:'POST',
        url:_this.apiUrl+'/e/wxapi/orderitem.php',
        data:obj,
        success:function(result){
        		_this.orderDetail = JSON.parse(result)[0];
            console.log(_this.orderDetail);
        },
        error : function(XmlHttpRequest, textStatus, errorThrown) {
            _this.utilMsg(XmlHttpRequest);
        }
    });
  },
  methods: {
  	//
	onSubmit:function(id){
		var _this = this;
		if(!localStorage.user){
    			_this.utilMsg("");
    			return false;
    		}
		var obj = {
			"ddid":_this.$route.params.id,  //id
	        "is_ctk":-1,  // -11
	        //is_buy_again:0,  //01
	        "czcoin":-1,    //   //-1
	        "txcoin":-1,     //   //-1
	        "tkcoin":-1      //   //-1
		};
		$("#msgLoding").show();
	    $.ajax({
	        type:'POST',
	        url:_this.apiUrl+'/e/wxapi/fukuan.php',
	        data:obj,
	        success:function(result){
	        		$("#msgLoding").hide();
	        		_this.utilMsg("")
	            console.log(result);
	            if(_this.orderDetail.type1==4){
	            		_this.addToQuanzi();
	            }
	        },
	        error : function(XmlHttpRequest, textStatus, errorThrown) {
	            _this.utilMsg(XmlHttpRequest);
	        }
	    });
	},
	//ios
	hrefTo:function(id){
		
	},
	//
	addToQuanzi:function(id){
		var _this = this;
		if(localStorage.user){
    			var user = JSON.parse(localStorage.user);
    		}else{
    			_this.utilMsg("");
    			return false;
    		}
		var data = {
			"fk_quanzi_id":_this.orderDetail.order_id,//id
			"fk_user_id":user.userid,  //id
	   };
	    $("#msgLoding").show();
	  	$.get(_this.apiUrl+'/e/wxapi/add_to_quanzi.php',data,function(res){
	  		$("#msgLoding").hide();
	  		_this.utilMsg('');
	  		if(_this.utilIsLocalhost()==false){
				var content = {"type":"hrefTo","link":"/circle/circle","title":''}
			    	var data = JSON.stringify(content);
			    ttf.nslog(data)
		  	}else{
		  		_this.$router.push("/circle/circle");
	  		}
		},'json')
	},
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.van-cell__value span{color: #FF1919;}
	.van-radio{display: inline-block;}
</style>



