<template>
  <div class="textHtml">
  	<van-cell-group>
	  <van-field v-model="username" label="" icon="clear" placeholder="" required @click-icon="username = ''"/>
	  <van-field v-model="password" label="" placeholder="" required/>
	</van-cell-group>
	<div class="addList" style="padding-bottom:80px;">
		<h3 class="imgH3" data-id0="" data-id1="" data-id2="">
  			<span style="color:red;">*</span>
  			<span class="inputLeft">:</span>
  		</h3>
  		<div style="color:#ccc;">&nbsp;&nbsp;&nbsp;</div>
  		<van-uploader :after-read="onRead0">
  			<div class="butIcons"></div>
		</van-uploader>
		<van-uploader :after-read="onRead1">
  			<div class="butIcons"></div>
		</van-uploader>
		<van-uploader :after-read="onRead2">
  			<div class="butIcons"></div>
		</van-uploader>
    </div>
	<van-cell-group>
	  <van-cell title="" is-link v-on:click="alipayFn(true)" />
	  <van-cell title="" is-link v-on:click="bankCardFn(true)" />
	</van-cell-group>
	
	<van-popup v-model="alipay" position="bottom" :overlay="true">
		<!---->
		<div >
			<h2 style="text-align:center;"></h2>
			<van-cell-group>
			  <van-field v-model="username" label="" icon="clear" placeholder="" @click-icon="username = ''"/>
			  <van-field v-model="password" label="" placeholder=""/>
			  <div class="butAdd but-80-red" style="margin: 40px auto;" v-on:click=""></div>
			</van-cell-group>
		</div>
	</van-popup>
	<van-popup v-model="bankCard" position="bottom" :overlay="true">
		<!---->
		<div>
			<h2 style="text-align:center;"></h2>
			<van-cell-group>
			  <van-field v-model="username" label="" icon="clear" placeholder="" @click-icon="username = ''"/>
			  <van-field v-model="password" label="" placeholder=""/>
			  <van-field v-model="password" label="" placeholder=""/>
			  <div class="butAdd but-80-red" style="margin: 40px auto;"  v-on:click=""></div>
			</van-cell-group>
		</div>
	</van-popup>
  </div>
</template>

<script>
export default {
  name: 'textHtml',
  props: ['value'],
  data () {
    return {
    	alipay:false,
    	bankCard:false,
    	msg: 'text',
    	username:'',
    	password:''
    }
  },
  created:function(){
    setTimeout(function(){
    		$(".van-uploader__input").css("height","100px");
    },500)
  },
  methods: {
  	onRead0: function (file) {
  		this.onRead(file,0);
  	},
  	onRead1: function (file) {
  		this.onRead(file,1);
  	},
  	onRead2: function (file) {
  		this.onRead(file,2);
  	},
    onRead: function (file,num) {
    	 var num = num;
      console.log(file)
      if(!file.file.name){return false;}
      var _this = this;
      $("#msgLoding").show();
		var formData = new FormData();
		formData.append("file", $('.van-uploader__input')[num].files[0]);
    		_this.utilAjaxFile('http://121.14.27.27:88/wp-json/wp/v2/media',formData,function(res){
    			$("#msgLoding").hide();
    			$(".butIcons").eq(num).hide();
    			$(".imgH3").attr("data-id"+num,res.id);
    			var img = $(".van-uploader").eq(num).find("img").attr("src");
		    if(img){
		      	$(".van-uploader").eq(num).find("img").attr("src",file.content);
		    }else{
		      	var imgHtml = '<img class="img" src="'+file.content+'" style="width:20rem;height:14rem; display: block;margin:0px auto;">'
		      	$(".van-uploader").eq(num).append(imgHtml)
		    }
		},"post");
    },
    alipayFn: function (type) {
  		this.alipay = type;
  		this.bankCard = false;
  	},
  	bankCardFn: function (type) {
  		this.bankCard = type;
  		this.alipay = false;
  	},
  }
}
</script>
<style scoped>
	.addList{margin-bottom: 10px;font-size: 1.3rem;background: #fff;overflow: hidden;}
	.addList textarea{width:96%;font-size: 1.3rem; height: 100px;padding:2%;border: 0;resize: none;outline:none;}
	.addList h3{background: #fff;padding: 10px 10px;margin: 0;font-size: 1.3rem;}
	.addList input{border: 0;font-size: 1.3rem;width: 75%;}
	.addList .inputLeft{width: 25%;}
	.van-uploader{margin:0px auto;display: block;position: relative;padding:2rem 0;
		background: url(../../assets/xiangji@3x.png) center 3rem no-repeat;background-size:3rem 3rem;
	}
	.butIcons{
		margin: 0 auto;width:10rem;height:3rem;padding-top:4.5rem;
		text-align:center;color: #ccc;z-index: 1;border: dashed 1px #ccc;
	}
</style>



